import Vue from 'vue'
import VueRouter from 'vue-router'
import BlShow from '@/components/BlShow'
import BlAdd from '@/components/BlAdd'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/show'
  },
  {
    path: '/show',
    component: BlShow
  },
  {
    path: '/add',
    component: BlAdd
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: 'myActive'
})

export default router
