<template>
  <div class="blShow">
    <BlItem v-for="(item, idx) in arrList" :key="idx" :myObj="item"></BlItem>
    <div class="btnGroup">
      <button @click="prev">&lt; 上一页</button>
      <span class="pageNum">{{ pageNum }}</span>
      <button @click="next">下一页 &gt;</button>
    </div>
  </div>
</template>

<script>
import BlItem from "./BlItem.vue";
export default {
  data() {
    return {
      arrList: [],
      pageNum: 1,
    };
  },
  components: {
    BlItem,
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData(p = 1) {
      this.axios
        .get("/showBlog.php", {
          params: {
            page: p,
          },
        })
        .then((res) => {
          this.arrList = res.data;
          document.body.scrollTop
            ? (document.body.scrollTop = 0)
            : (document.documentElement.scrollTop = 0);
        });
    },
    prev() {
      if (this.pageNum <= 1) {
        alert("已经是第一页了");
        return;
      }
      this.pageNum--;
      this.getData(this.pageNum);
    },
    next() {
      if (this.arrList.length < 9) {
        alert("已经没有更多的数据了");
        return;
      }
      this.pageNum++;
      this.getData(this.pageNum);
    },
  },
};
</script>

<style lang="less" scoped>
.blShow {
  padding: 50px 30px;
}
.btnGroup {
  padding: 20px 0;
  display: flex;
  justify-content: center;
  button {
    border: 1px solid #41b883;
    background-color: #fff;
    color: #41b883;
    font-size: 18px;
    border-radius: 3px;
    padding: 5px 10px;
    margin: 0 3px;
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    &:hover {
      background-color: #41b883;
      color: #fff;
    }
  }
  span {
    padding: 5px 10px;
  }
}
</style>
