import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
// 定义默认路径
axios.defaults.baseURL = 'https://ku.qingnian8.com/dataApi/blog'
// 定义Vue公共属性Vue
Vue.prototype.axios = axios
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
