export default {
  // 截取字符串
  // val为字符串，len为需求长度
  ReStrlen: function (val, len) {
    // 如果字符串长度大于需求长度则返回截取需求长度的字符串，反之返回原样字符串
    return val.length > len ? val.substr(0, len) + '...' : val
  },
  // 时间戳转换为时间
  // val为时间戳，type为输出的样式选择默认为0
  myDate: function (value, type = 0) {
    // 获得时间val为10位的时间戳，val*1000是为了JS处理13位的时间戳
    var time = new Date(value * 1000)
    // 获得年份
    var year = time.getFullYear()
    // 获得月份，默认从0开始
    var month = time.getMonth() + 1
    // 获得天数
    var date = time.getDate()
    // 获得小时
    var hour = time.getHours()
    // 获得分钟
    var minute = time.getMinutes()
    // 获得秒数
    var second = time.getSeconds()
    // 对月、天数、小时、分钟、秒数进行处理，即小于10则在前面补充0，反之原样
    month = month < 10 ? '0' + month : month
    date = date < 10 ? '0' + date : date
    hour = hour < 10 ? '0' + hour : hour
    minute = minute < 10 ? '0' + minute : minute
    second = second < 10 ? '0' + second : second
    // 将时间样式传入数组
    var arr = [
      year + '-' + month + '-' + date,
      year + '-' + month + '-' + date + ' ' + hour + ':' + minute + ':' + second,
      year + '年' + month + '月' + date,
      year + '年' + month + '月' + date + ' ' + hour + ':' + minute + ':' + second,
      hour + ':' + minute + ':' + second
    ]
    // 返回数组里的时间样式
    return arr[type]
  }
}
