<template>
  <div class="BlAdd">
    <div class="addMain">
      <div class="row">
        <div class="text">博客标题：</div>
        <div class="iptBox">
          <input type="text" placeholder="请输入标题" v-model="blObj.title" />
        </div>
      </div>
      <div class="row">
        <div class="text">作者：</div>
        <div class="iptBox iptBoxAuthor">
          <input
            type="text"
            placeholder="请输入作者名称"
            v-model="blObj.author"
          />
        </div>
      </div>
      <div class="row">
        <div class="text">博客分类：</div>
        <div class="iptBox">
          <select v-model="blObj.classify">
            <option
              :value="item.classname"
              v-for="item in arrList"
              :key="item.id"
            >
              {{ item.classname }}
            </option>
          </select>
        </div>
      </div>
      <div class="row rowArea">
        <div class="text">主体内容：</div>
        <div class="iptBox">
          <textarea placeholder="请输入内容" v-model="blObj.content"></textarea>
        </div>
      </div>
      <div class="row">
        <div class="text"></div>
        <div class="iptBox">
          <button @click="onSubmit">提交</button>
        </div>
      </div>
    </div>
    <div class="addShow" v-show="isShow">
      <h2>博客预览</h2>
      <BlItem :myObj="blObj"></BlItem>
    </div>
  </div>
</template>

<script>
// 导入BlItem组件
import BlItem from "./BlItem.vue";
export default {
  data() {
    return {
      // 定义blobj对象
      blObj: {
        title: "",
        // 将时间戳转换为10位
        posttime: new Date().getTime() / 1000,
        classify: "",
        author: "",
        content: "",
      },
      // 定义数组
      arrList: [],
    };
  },
  components: {
    // 组件
    BlItem,
  },
  mounted() {
    // 请求
    this.axios.get("/classBlog.php").then((res) => {
      this.arrList = res.data;
    });
  },
  computed: {
    // 计算属性
    isShow() {
      if (
        this.blObj.title ||
        this.blObj.author ||
        this.blObj.classify ||
        this.blObj.content
      ) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    // 提交函数
    onSubmit() {
      for (var item in this.blObj) {
        if (this.blObj[item] == "") {
          alert("请补充完整内容");
          return;
        }
      }
      this.axios
        .get("/addBlog.php", {
          params: this.blObj,
        })
        .then((res) => {
          if (res.data.code == 200) {
            alert("发布成功");
            this.$router.push("/");
          } else {
            alert("请补充完整内容");
          }
        });
    },
  },
};
</script>
<style lang="less" scoped>
.BlAdd {
  padding: 50px 30px;
  .addMain {
    padding: 0 100px;
    .row {
      display: flex;
      padding: 10px 0;
      align-items: center;
      .text {
        width: 20%;
        text-align: right;
        padding-right: 20px;
        font-size: 18px;
        color: #333;
      }
      .iptBox {
        width: 80%;
        input,
        select,
        textarea,
        button {
          outline: none;
          width: 100%;
          height: 36px;
          border: 1px solid #41b883;
          border-radius: 5px;
          padding: 0 10px;
          font-size: 16px;
          color: #666;
          transition: 0.3;
          &:focus {
            box-shadow: 0 0 10px rgba(65, 184, 131, 0.3);
          }
        }
        select {
          width: 50%;
        }
        textarea {
          padding: 10px;
          height: 150px;
        }
        button {
          width: 50%;
          background-color: #41b883;
          color: #fff;
          cursor: pointer;
          &:hover {
            background-color: #2f6f52;
          }
        }
      }
      .iptBoxAuthor {
        input {
          width: 50%;
        }
      }
    }
    .rowArea {
      align-items: flex-start;
    }
  }
  .addShow {
    padding-top: 50px;
    h2 {
      border-top: 1px solid #ccc;
      padding: 50px 0;
      text-align: center;
      font-size: 25px;
      color: #555;
    }
  }
}
</style>
